import { Component, OnInit } from '@angular/core';
import { BannerService } from '../../../../../auth/_services/banner.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../../../../../_services/data.service';
// import { NotificationsService } from 'angular4-notify';
import { ToastrService } from 'ngx-toastr';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'app-banner-list',
    templateUrl: "banner-list.component.html",
    styles: []
})
export class BannerListComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;
    constructor(private imgBaseUrl: ScriptLoaderService, private router: Router, private toastr: ToastrService, private _bannerService: BannerService, private _dataService: DataService, private formBuilder: FormBuilder) { }
    public brandData;
    public searchValue = '';
    public brandDrpdown: any;
    public dropDownValue = '';
    public selectedStatus = '';
    public dataSaved = false;
    public saveMsg = '';
    brandImgUrl: any;
    p: number = 1;
    public ImgUrl: any;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.website_setting != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // this.notificationsService.addInfo('Brand Added Successfully');
        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('brand');
        this.ImgUrl = this.imgBaseUrl._imgBaseUrl;

        localStorage.setItem('section', 'brand');
        this._bannerService.getAll().subscribe(
            data => {
                this.brandData = data.banner;

            }

        );


    }











    public brand_delete(del_id) {


        console.log(del_id);
        let value = confirm('Are u want to delete this brand?');
        if (value) {
            this._bannerService.bannerDelete(del_id).subscribe(
                data => {
                    this.brandData = data.data;
                }
            );

        }
    }


    public changeBannerStatus(banner: any) {

        console.log(banner);

        if (banner.status == 'Active') {
            banner.status = 'Inactive';
            this._bannerService.updateStatus(banner.pk_banner_id).subscribe(

                data => {

                }
            );
        }
        else {
            banner.status = 'Active';
            this._bannerService.updateStatus(banner.pk_banner_id).subscribe(

                data => {

                }
            );
        }
    }

}
